import styled from "@emotion/styled";

export const Modal = styled.div`
  top: 0;
  display: flex;
  justify-content: center;
  text-align: center;
  border: 2px solid #777;
  border-radius: 20px;
  width: 500px;
  height: 600px;
  .close-modal {
    width: 60px;
    height: 30px;
    font-size: 15px;
    border-radius: 5px;
    background-color: #c14b45;
    display: flex;
    justify-content: center;
  }
`;
